	export default {
		data() {
			return {
				arr:[],
				arr1:[],
				arr2:[],
				value:"",
				value1:"",
				value2:"",
				caseList:[],
				onMenuId:'',	
				options:[
				{value:0,label:'审核中'},{value:1,label:'已审核'},{value:2,label:'已签合同'},{value:3,label:'已结案'},{value:-2,label:'返回修改'},{value:4,label:'待结案'}
					],
				Sort:1,
				SortArr:[{value:1,label:'降序'},{value:2,label:'升序'}],
			}
		},
		onLoad() {
			var _self = this
			_self.getMineList()
			  uni.getSystemInfo({
        success: (res)=> {
            this.windowHeight = res.windowHeight;
        }
    });    
    uni.onWindowResize((res) => {
        if(res.size.windowHeight < this.windowHeight){
            this.tabbar = false
        }else{
            this.tabbar = true
        }
    })

			
		},
		onShow(){
			var _self = this
			_self.getOneMenu()
		},
		methods: {
			getMineList:function(){
				var _self =this
				var state,state1
				console.log(_self.value2)
				if(_self.value2 === ""){
						state = -3
				}else{
					state = _self.value2
				}
				if(_self.value1 == ""){
					state1 = 0
				}else{
					state1 = _self.value1
				}
				uni.request({
					url:_self.$api1+'Index/Show_All_Case',
					data:{
						UserId:uni.getStorageSync("userId"),
						Dic_Id:state1,
						Status:state,
						Sort:_self.Sort,	
					},
					success:function(res){
						console.log(res)
						_self.caseList = res.data.All_Case
					}
				})
				
			},
			getOneMenu:function(){
				var _self= this
				uni.request({
					url:_self.$api1+'Index/GetBoxOne',
					data:{},
					success:function(res){
						console.log(res)
						_self.arr = res.data
					}
				})
			},
			getTwoMenu:function(id){
				var _self= this
				uni.request({
				url:_self.$api1+'Index/GetBoxTwo',
				data:{Id:id},
				success:function(res){
					console.log(res)
					_self.arr1 = res.data
					_self.value1 = res.data[0].Id
				},
				})
				
			},
			changeOneMenu(e){
				var _self =this
				console.log(e)
				if(e===0){
					_self.value2 = ""
					_self.value1 = ""
					_self.value=""
					_self.arr1 = []
					_self.getMineList()
				}else{
					_self.value = e
					_self.getTwoMenu(e)
				}
			},
			changeTwoMenu(e){
				this.value1 = e
				this.getMineList()
			},
			changeStatus(e){
				console.log(e)
				this.value2 = e
				this.getMineList()
			},
			changeSort(e){
				this.getMineList()
			},
			toAdd(){
				uni.navigateTo({
					url:'../../pages/caseAdd/caseAdd',
				})
			},
			gotoEdit(id,type){
					// console.log(id+++"/"+type)
					uni.navigateTo({
						url:'../../pages/caseEdit/caseEdit?Id='+id+'&Charging_Method='+type
					})
			},
			updateData(id,type){
				uni.navigateTo({
					url:'../../pages/caseUpdate/caseUpdate?Id='+id+'&Charging_Method='+type
				})
			}
		},
		watch:{
			value1:function(newData,old){
				this.getMineList()
			},
		}
	}
